import React, {Component} from 'react';
import ImageGallery from 'react-image-gallery';
import "react-image-gallery/styles/css/image-gallery.css";
import './App.css';

class App extends Component {

    constructor() {
        super(Component);
        this.ORIENTATION_PORTRAIT = 0;
        this.ORIENTATION_LANDSCAPE = 1;
        this.updateStyle = this.updateStyle.bind(this);
        this.generateContainerStyles = this.generateContainerStyles.bind(this);
        this.handleDeviceOrientationChange = this.handleDeviceOrientationChange.bind(this);
        this.calculateDimensions = this.calculateDimensions.bind(this);

        this.state = {
            containerStyles: this.generateContainerStyles(),
            images: Array.from(Array(9).keys())
                .map(function (x) {
                    return {
                        original: "./assets/mihalykoles/g_koles_mihaly_0000" + (x + 1) + ".jpeg",
                        thumbnail: "./assets/mihalykoles/g_koles_mihaly_0000" + (x + 1) + ".jpeg"
                    }
                }),
            orientation: this.getDeviceOrientation(),
        };
    }

    componentDidMount() {
        // Additionally I could have just used an arrow function for the binding `this` to the component...
        window.addEventListener("resize", this.updateStyle);
        window.addEventListener("orientationchange", this.handleDeviceOrientationChange)
    }

    getDeviceOrientation() {
      if (Math.abs(window.orientation) === 90) {
        return this.ORIENTATION_LANDSCAPE;
      } else {
        return this.ORIENTATION_PORTRAIT;
      }
    }

    handleDeviceOrientationChange() {
      this.setState({orientation: this.getDeviceOrientation()});
      this.updateStyle();
    }

    calculateDimensions() {
      var img_ratio = 16/9;
      if (this.state) {
        var image = document.createElement('img');
        image.src = this.state.images[0].original;
        img_ratio = image.naturalWidth / image.naturalHeight;
      }
      var newWidth = window.innerHeight*img_ratio;
      if (newWidth > window.innerWidth) {
        newWidth = window.innerWidth;
      }

      return {
        height: window.innerHeight,
        width: newWidth,
      };
    }

    generateContainerStyles() {
      let sizes = this.calculateDimensions();
      if (this.getDeviceOrientation() === this.ORIENTATION_LANDSCAPE) {
        return {
          width: `${sizes.width}px`,
          height: `${window.screen.availHeight}px`,
          margin: 'auto'
        };
      } else {
        return {
          width: `${sizes.width}px`,
          height: `${sizes.height}px`,
          margin: 'auto'
        };
      }
    }

    updateStyle() {
        this.setState({
            containerStyles: this.generateContainerStyles()
        });
    }

    componentWillUnmount() {
        window.removeEventListener("resize", this.updateStyle);
    }

    render() {
        return (
            <div style={this.state.containerStyles}>
                <ImageGallery items={this.state.images} slideInterval={10000}
                  showThumbnails={this.state.orientation === this.ORIENTATION_PORTRAIT}
                  showNav={this.state.orientation === this.ORIENTATION_PORTRAIT}
                  showFullscreenButton={this.state.orientation === this.ORIENTATION_PORTRAIT || true}
                  showPlayButton={this.state.orientation === this.ORIENTATION_PORTRAIT}
                />
            </div>
        );
    }
}

export default App;
